@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
	vertical-align: baseline;
    outline: 0;
    font-family: 'Roboto', sans-serif;
}
/* HTML5 display-role reset for older browsers */
body{
	margin: 0;
	padding: 0;
}

.date-animation{
    -webkit-animation: appear 2s;
            animation: appear 2s;
}

@-webkit-keyframes appear{
    from {opacity: 0.01}
    to {opacity: 1}
}

@keyframes appear{
    from {opacity: 0.01}
    to {opacity: 1}
}

.Cu{
    background-color: #F4DFEB;
}

.Pr{
    background-color: #DDEBF1;
}

.HC{
    background-color: #FDECCD;
}

.DC{
    background-color: #F6D0E5;
}

.C{
    background-color: #DED2F6;
}



#Calender .header-container{
    margin: 0;
    /* background-color: aqua; */
    box-shadow:0px 1px 3px 0px rgba(0,0,0,0.15);
    padding: 10px 25% 0px;
    font-size: 25px;
}

#Calender .month{
    font-weight: 900;
    
}

#Calender .week-row{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin-top: 5px;
}

#Calender .week-item{
    padding: 5px 15px;
    width: 10vw;
    padding-bottom: 5px;
}

#Calender .current-date{
    display: flex;
    justify-content: space-between;
}

#Calender .today-btn{
    background-color: cadetblue;
    padding: 0px 20px;
    border-radius: 10px;
    font-size: small;
    font-weight: 700;
    margin-right: 15px;
}

#Calender .day-btn{
    background-color: cadetblue;
    border-radius: 10px;
    font-size: small;
    font-weight: 900;
    cursor: pointer;
}
#Calender .day-btn:hover,#Calender .today-btn:hover{
    background-color: grey;
}

#Body .week-row{
    display: flex;
    margin: 0 25%;
    padding: 0;
}

#Body .day{
    height: 14.5vh;
    width: 9vw;
    border: 1px solid black;
    /* background-color: blueviolet; */
    font-size: 25px;
    float: right;
}

#Body .day-element{
    float: right;
    margin-top: 5px;
    margin-right: 5px;
}

#Body .sunday{
    background-color: #E4E6E8;
}

#Body .opaque{
    opacity: 0.4;
    pointer-events: none;
}

#Body .highlight-today{
    color: red;
}
#Event{
    height: 100%;
    width: 5vw;
    cursor: pointer;
    
}

#Event .header{
    margin-top: -5px;
    width: 7vw;
    display: flex;
    justify-content: space-between;
}

#Event .filled-star{
    font-size: 12px;
    color: aqua;
    border: none;
}

#Event .not-filled-star{
    font-size: 12px;
    color: rgb(167, 164, 164);
    border: none;
}

#Event .daynum{
    margin-top: 10px;
    font-size: 14px;
    margin-right: 5px;
}

#Event .img{
    width: 7vw;
    height: 7vh;
}

#Event .typeOfDay{
    width: 7vw;
    display: flex;
    justify-content: center;
}

#Event .dayType{
    padding: 3px 3px;
    border-radius: 50%;
    font-size: 9px;
    box-sizing: border-box;
    margin-top: -2px;
    color: black;
    margin-right: 5px;
}


#Event .preloader{
	background-color: rgba(39, 39, 41, 0.7);
	width:100%;
	height: 100%;
	position: fixed;
	top:0;
	left:0;
    z-index:99999999999999;
}

#Event .close-back{
    height: 50px;
    width: 50px;
    position: absolute;
    right: 15px;
    background-color: white;
    top: 15px;
    border-radius: 50%;
}

#Event .close{
    color: #1FA2FF;
    font-size: 50px;
}
#Carousel{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

#Carousel .card{
    height: 70vh;
    width: 25vw;
    display: table-cell;
    vertical-align: middle;
    background-color: white;
}

#Carousel .iterator{
    font-size: 100px;
}

#Carousel .img{
    width: 100%;
    height: 65%;
}

#Carousel .disable{
    opacity: 0.3;
    pointer-events: none;
}

#Carousel .highlights{
    width: 24vw;
    display: flex;
    justify-content: space-between;
    margin: 0px 10px;
}

#Carousel .typeDay{
    display: flex;
}

#Carousel .dayType{
    text-align: center;
    border-radius: 50%;
    font-size: 14px;
    box-sizing: border-box;
    margin-top: -2px;
    color: black;
    margin-right: 5px;
    padding: 0px 8px;
    padding-top: 7px;
}

#Carousel .date-now{
    margin-top: 10px;
    font-weight: bolder;
    margin-left: 10px;
    font-size: 20px;
}

#Carousel .desc{
    margin: 8px 12px;
    text-align: justify;
    font-size: 12px;
    font-weight: 200;
}
